`, "",{"version":3,"sources":["webpack://./src/components/PowerSimulator.vue"],"names":[],"mappings":";AA2DE;IACE,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,WAAW;IACX,oBAAoB;AACtB;AACA;IACE,WAAW;IACX,kBAAkB;IAClB,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,cAAc;AAChB;AACA;IACE,UAAU;IACV,UAAU;AACZ;AACA;IACE,WAAW;IACX,gBAAgB;IAChB,kBAAkB;IAClB,iBAAiB;AACnB","sourcesContent":["<script setup lang=\"ts\">\n  import { store } from '@/store';\n  import { ref, watch } from 'vue';\n  import { i18n } from '@/assets/i18n';\n\n  const MIN_WATT = 0;\n  const MAX_WATT = 500;\n\n  const props = defineProps({\n    debug: Boolean,\n    watt: Number\n  });\n\n  const simulatedWatt = ref(MIN_WATT);\n\n  watch(simulatedWatt, () => \n    store.power.setDebugWatt(simulatedWatt.value)\n  );\n\n  function getBackgroundColor(): string {\n    const calcWatt = (props.debug ? simulatedWatt.value : props.watt) || 0;\n    const clamped = Math.max(0, Math.min(MAX_WATT,calcWatt));\n\n    const green = { r: 182, g: 242, b: 195 }; \n    const yellow = { r: 255, g: 255, b: 102 }; \n    const red = { r: 139, g: 0, b: 0 };       \n\n    let start, end, t;\n\n    if (clamped <= MAX_WATT / 2) {\n      start = green;\n      end = yellow;\n      t = clamped / (MAX_WATT / 2);\n    } else {\n      start = yellow;\n      end = red;\n      t = (clamped - (MAX_WATT / 2)) / (MAX_WATT / 2);\n    }\n\n    return `background-color: rgba(${\n      Math.round(start.r + (end.r - start.r) * t)\n    }, ${\n      Math.round(start.g + (end.g - start.g) * t)\n    }, ${\n      Math.round(start.b + (end.b - start.b) * t)\n    }, 0.5)`;\n  }\n\n</script>\n\n<template>\n  <div class=\"power\" :style=\"getBackgroundColor()\">\n    <h3>{{ i18n('POWERSIM_LABEL') }}</h3>\n    <span class=\"watts\">{{ watt + ' ' + i18n('POWERSIM_WATT') }}</span>\n    <input type=\"range\" v-if=\"debug \"v-model=\"simulatedWatt\" :min=\"MIN_WATT\" :max=\"MAX_WATT\"></input>\n  </div>\n</template>\n\n<style scoped>\n  .power {\n    position: absolute;\n    right: 2em;\n    bottom: 5em;\n    width: 12em;\n    border-radius: 0.5em;\n  }\n  .watts {\n    width: 100%;\n    text-align: center;\n    display: block;\n    font-size: 1.5em;\n    font-weight: 700;\n    padding: 0.2em;\n  }\n  input {\n    width: 90%;\n    margin: 5%;\n  }\n  h3 {\n    width: 100%;\n    font-size: 0.7em;\n    text-align: center;\n    margin-top: 0.5em;\n  }\n</style>\n"],"sourceRoot":""}]);
